@media (max-width: 768px) {
    .profile-social .fa-lg {
        font-size: 3.2em;
        padding: 4px;
    }
}

@media (min-width: 768px) {
    .profile-social .fa-lg {
        font-size: 1.5em;
    }
}


body .App {
    color: rgb(44, 44, 44);
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    margin: 0;
}


a {
    color: rgb(83, 83, 204);
    text-decoration: solid;
}


.App {
    text-align: center;
    position: relative;
    min-height: 100vh;
}

.App-logo {
    height: 20vmin;
    pointer-events: none;
}

.App-header {
    background-color: #404247;
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
    margin-bottom: 20px;
}

.main {
    margin: auto;    
    max-width: 900px;
    position: relative;
}

.main-title {
    font-size: 2em;
    color: white;
}

.sub-title {
    color: white;
    font-size: 1.1em;
}

.menu li {
    display: inline-block;
    padding: 15px;
    border: solid rgb(226, 226, 226) 2px;
    border-radius: 4px;
    margin-top: 5px;
    margin-right: 20px;
    margin-bottom: 5px;
    font-size: 20px;
}
.menu li:hover {
    background-color: rgb(235, 235, 235);
    cursor: pointer;
}

.main-page.row {
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;

    padding: 0;
    list-style: none;
}

.main-page .profile {
    max-width: 256px;
    margin: auto;
}

.social {
    text-align: left;
}

img.profile-pic {
    width: 100%;
    height: auto;
    min-width: 48px;
    border-radius: 15px;
}

.main-page .description {
    width: 60%;
    margin: auto;
    margin-top: 10px;
    text-align: left;
    font-size: larger;
}

.profile svg {
    color: #595959
}

.profile .item svg {
    width: 30%;
    margin: auto;
    text-align: left;
}
.profile .item a {
    width: 70%;
    margin: auto;
    text-align: left;
}

nav {
    font-size: 1.5em;
}

footer {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 20px;
    background-color: #232425;
    color: white;
    position: relative;
    bottom: 0;
    height: 5.5rem;
    width: 100%;
}

footer div {
    margin: auto;
}

footer div a:visited {
    color: rgb(207, 132, 230);
}

footer div a {
    color: rgb(192, 178, 196);
}


div .project-container {
    padding: 5px;
    transition: all 1s;
}

.profile-social div {
    margin: auto;
}

.project ul {
    padding-left: 0px;
}

div .project {
    border-radius: 5px;
    border: solid 1px rgb(156, 156, 156);
    padding: 8px;
    transition: box-shadow 0.15s ease-in;
    /* margin-top: 5px;
    padding: 5px; */
    box-shadow: 2px 2px 8px 5px rgb(230, 230, 230);
    height: 100%;
}
.project:hover {
    box-shadow: 2px 2px 8px 5px rgb(168, 168, 168);
}

.project svg {
    font-size: 1.1em;
}

.project img {
    max-width: 100%;
    margin-bottom: 5px;
    margin-top: 5px;
    margin-left: auto;
    margin-right: auto;
    max-height: 200px;
}

.project .about {
    text-align: left;
}

.project li {
    margin-left: 25px;
}

.project .preview {
    margin: auto;
    text-align: center;
}

.project .row {
    margin: 0px;
}

.project .date {
    position: absolute;
    right: 0;
    top: 0;
}

.research {
    text-align: left;
    border-radius: 5px;
    border: solid 1px rgb(156, 156, 156);
    padding: 8px;
    transition: box-shadow 0.15s ease-in;
    /* margin-top: 5px;
    padding: 5px; */
    box-shadow: 2px 2px 8px 5px rgb(230, 230, 230);
}

div .research-container {
    padding: 5px;
    transition: all 1s;
}

.research:hover {
    box-shadow: 2px 2px 8px 5px rgb(168, 168, 168);
}

.research-authors {
    font-style: italic;
}
.research-title {
    font-weight: bold;
}

.expandable {
    cursor: pointer;
}

.content .tab-content {
    padding-top: 20px;
    min-height: 780px;
}